import {Reducer} from 'redux'

import {SignInTypes} from './types'
import {IAuthStore} from '../../interfaces/Store'

export const initialState: IAuthStore = {
  isAuth: false,
  isFetch: false,
  email: '',
  profile: {
    fullName: ''
  }
}

export const $authReducer: Reducer<IAuthStore> = (state = initialState, action) => {
  console.log(action)
  switch (action.type) {
    case SignInTypes.start: {
      return {...state, isFetch: true}
    }
    case SignInTypes.logout: {
      return initialState
    }
    case SignInTypes.success: {
      return {
        isAuth: true,
        isFetch: false,
        ...action.payload
      }
    }
    case SignInTypes.failure: {
      return {
        ...state,
        isAuth: false,
        isFetch: false,
        errors: action.payload
      }
    }
    default: {
      return state
    }
  }
}
