import * as Pages from '../pages'
import {RouteProps} from 'react-router-dom'
import {IPrivateRouteProps} from '../interfaces/Router'

const publicRoutes: RouteProps[] = [
  {path: '/', exact: true, component: Pages.Home},
  {path: '/github', component: Pages.Github},
  {path: '/code', component: Pages.Code}
]
const privateRoutes: IPrivateRouteProps[] = [
  {path: '/settings', redirectPath: '/login', component: Pages.Settings},
  {path: '/profile', redirectPath: '/login', component: Pages.Profile}
]

export default {
  publicRoutes,
  privateRoutes
}
