/**
 * Root Api
 */
const apiDev = 'http://localhost:7777'
const apiProd = 'http://localhost:7777' // 'https://devs9-kit-backend.herokuapp.com'

export const ROOT_API = process.env.NODE_ENV === 'production' ? apiProd : apiDev

/**
 * Api Branches
 */
const AUTH_API = ROOT_API + '/api/auth'

/**
 * Auth Api Urls
 */
export const authUrls = {
  login: `${AUTH_API}/login`,
  signIn: `${AUTH_API}/sign_in`,
  signUp: `${AUTH_API}/sign_up`,
  signInGoogle: `${AUTH_API}/sign_in_google`,
  signUpGoogle: `${AUTH_API}/sign_up_google`
}
