import React, {FC, useState, useEffect} from 'react'
import {useHistory} from 'react-router-dom'
import {useDispatch} from 'react-redux'

import CloseIcon from '@material-ui/icons/Close'
import Container from '@material-ui/core/Container'
import Typography from '@material-ui/core/Typography'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'

import {AuthForm} from './AuthForm'
import {
  AvatarStyled,
  BarButtonsStyled,
  IconSocialStyled,
  ButtonCloseStyled,
  useAuthFormStyles as useStyles
} from './themes'

import {imgPath} from '../../../constants'
import {signInText, signUpText} from './config/auth'
import {googleSaga} from '../../../store/auth'
import {IAuthLayout} from './ts'

export const AuthLayout: FC<IAuthLayout> = () => {
  const classes = useStyles()
  const history = useHistory()
  const dispatch = useDispatch()

  const [isSignIn, setIsSignIn] = useState(true)
  const [texts, setTexts] = useState(isSignIn ? signInText : signUpText)

  const handleGoogleAuth = () => {
    dispatch(googleSaga())
  }
  const handleClickClose = () => {
    history.push('/')
  }
  const handleToggleSignIn = () => {
    setIsSignIn(!isSignIn)
  }

  useEffect(() => {
    setTexts(isSignIn ? signInText : signUpText)
  }, [isSignIn])

  return (
    <Container component="main" className={classes.root}>
      <ButtonCloseStyled onClick={handleClickClose}>
        <CloseIcon />
      </ButtonCloseStyled>
      <AvatarStyled>
        <LockOutlinedIcon />
      </AvatarStyled>
      <AuthForm isSignIn={isSignIn} textButton={texts.button} />
      <IconSocialStyled src={imgPath.googleIcon} onClick={handleGoogleAuth} />
      <BarButtonsStyled>
        <Typography component="span" variant="subtitle2">
          {texts.isHaveAccount}
        </Typography>
        <Typography
          color="primary"
          component="span"
          variant="subtitle2"
          className="isAccountText"
          onClick={handleToggleSignIn}>
          {texts.goTo}
        </Typography>
      </BarButtonsStyled>
    </Container>
  )
}
