import React, {ChangeEvent, FC, useState} from 'react'
import {useSelector, useDispatch} from 'react-redux'
import {useHistory} from 'react-router-dom'
import Cookies from 'js-cookie'

import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import IconButton from '@material-ui/core/IconButton'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import AccountCircle from '@material-ui/icons/AccountCircle'

import {logout} from '../../../store/auth'
import {IStore} from '../../../interfaces/Store'

export const AccountMenu: FC<{hideDrawer: () => void}> = ({hideDrawer}) => {
  const history = useHistory()
  const dispatch = useDispatch()
  const $isAuth = useSelector((state: IStore) => state.$authReducer.isAuth)

  const [anchorEl, setAnchorEl] = useState(null)

  const goToAuth = () => {
    history.push('/login')
    setAnchorEl(null)
    hideDrawer()
  }
  const handleClose = () => {
    setAnchorEl(null)
  }
  const handleLogout = () => {
    Cookies.remove('Authorization', {path: ''})
    dispatch(logout())
    handleClose()
  }
  const goToProfile = () => {
    history.push('/profile')
    handleClose()
  }
  const handleMenuClick = (event: ChangeEvent<any>) => {
    setAnchorEl(event.currentTarget)
  }

  return (
    <div className="btnGroup">
      {$isAuth ? (
        <div>
          <IconButton
            style={{paddingRight: 10}}
            color="inherit"
            aria-haspopup="true"
            onClick={handleMenuClick}
            aria-controls="menu-appbar">
            <AccountCircle style={{fontSize: '1.4em'}} />
          </IconButton>
          <Menu
            keepMounted
            id="menu-appbar"
            anchorEl={anchorEl}
            onClose={handleClose}
            open={Boolean(anchorEl)}>
            <MenuItem onClick={goToProfile}>Profile</MenuItem>
            <MenuItem onClick={handleLogout}>Exit</MenuItem>
          </Menu>
        </div>
      ) : (
        <IconButton aria-label="open account" onClick={goToAuth}>
          <ExitToAppIcon />
        </IconButton>
      )}
    </div>
  )
}
