import React, {useEffect, useState} from 'react'
import {BrowserRouter} from 'react-router-dom'
import {useDispatch} from 'react-redux'
import Cookie from 'js-cookie'
import Paper from '@material-ui/core/Paper'

import {RootRouter} from '../router'
import {loginSaga} from '../store/auth'
import {Drawer, AppBar, useAppLayoutS} from '../components/organisms'

export default function App() {
  const dispatch = useDispatch()
  const classes = useAppLayoutS()

  const [isShowDrawer, setShowDrawer] = useState(false)

  const showDrawer = () => {
    setShowDrawer(true)
  }
  const hideDrawer = () => {
    setShowDrawer(false)
  }

  useEffect(() => {
    if (Cookie.get('Authorization')) {
      dispatch(loginSaga())
    }
  }, [dispatch])

  return (
    <Paper className={classes.paper}>
      <BrowserRouter>
        <Drawer isShow={isShowDrawer} hideDrawer={hideDrawer} />
        <AppBar isShow={isShowDrawer} showDrawer={showDrawer} hideDrawer={hideDrawer} />
        <RootRouter />
      </BrowserRouter>
    </Paper>
  )
}
