import React, {FC, ChangeEvent, useEffect, useState} from 'react'
import {useDispatch} from 'react-redux'

import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import TextField, {TextFieldProps} from '@material-ui/core/TextField'

import {authSendButtonS} from './themes'
import {signInState, signUpState, signInFields, signUpFields} from './config/auth'
import {signInSaga, signUpSaga} from '../../../store/auth'
import {ISignInPayload, ISignUpPayload} from '../../../interfaces/Store'
import {IAuthForm} from './ts'

export const AuthForm: FC<IAuthForm> = ({isSignIn, textButton}) => {
  const dispatch = useDispatch()

  const [key, setKey] = useState(1)
  const [fields, setFields] = useState(isSignIn ? signInFields : signUpFields)
  const [fieldsState, setFieldsState] = useState(isSignIn ? signInState : signUpState)

  const handleFieldChange = (event: ChangeEvent<HTMLInputElement>) => {
    const {
      target: {value, name}
    } = event

    setFieldsState((prevState) => ({...prevState, [name]: value}))
  }
  const handleSendForm = () => {
    isSignIn
      ? dispatch(signInSaga(fieldsState as ISignInPayload))
      : dispatch(signUpSaga(fieldsState as ISignUpPayload))
  }

  useEffect(() => {
    setKey(key + 1)
    setFieldsState(isSignIn ? signInState : signUpState)
    setFields(isSignIn ? signInFields : signUpFields)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isSignIn])

  return (
    <form noValidate>
      <Grid container spacing={2} key={key}>
        {fields.map(({id, type, label, name, required, fullWidth}: TextFieldProps) => (
          <Grid item xs={12} sm={!fullWidth && 6} key={id}>
            <TextField
              fullWidth
              id={id}
              name={name}
              type={type}
              label={label}
              variant="outlined"
              required={required}
              autoComplete={type}
              onChange={handleFieldChange}
            />
          </Grid>
        ))}
      </Grid>
      <Button
        fullWidth
        color="primary"
        variant="contained"
        children={textButton}
        style={authSendButtonS}
        onClick={handleSendForm}
      />
    </form>
  )
}
