import React, {FC} from 'react'
import AppBarMui from '@material-ui/core/AppBar'
import IconButton from '@material-ui/core/IconButton'
import ChevronLeft from '@material-ui/icons/ChevronLeft'
import clsx from 'clsx'

import {AccountMenu} from '../Menus'
import {imgPath} from '../../../constants'
import {ToolbarStyled, useAppBarS as useStyle} from './themes'
import {IAppBar} from './ts'

export const AppBar: FC<IAppBar> = ({isShow, showDrawer, hideDrawer}) => {
  const classes = useStyle()

  const handleClickLink = () => {
    isShow ? hideDrawer() : showDrawer()
  }

  return (
    <AppBarMui position="fixed" className={clsx(classes.appBar, {[classes.appBarShift]: isShow})}>
      <ToolbarStyled>
        <IconButton className="logo" aria-label="open drawer" onClick={handleClickLink}>
          <img src={imgPath.logoPng} alt="logo" />
        </IconButton>
        {isShow && (
          <IconButton className="left" aria-label="hide drawer" onClick={hideDrawer}>
            <ChevronLeft />
          </IconButton>
        )}
        <AccountMenu hideDrawer={hideDrawer} />
      </ToolbarStyled>
    </AppBarMui>
  )
}
