import {all, call, fork, put, takeEvery, takeLatest} from 'redux-saga/effects'
import {identity, pickBy} from 'lodash'
import {toast} from 'react-toastify'
import Cookies from 'js-cookie'

import {SignInTypes} from './types'
import * as authAction from './actions'
import {fetchRequest, authUrls} from '../../api'
import {SignInRequestActionT, SignUpRequestActionT} from '../../interfaces/Store'

/**
 * Sagas
 */
function* login() {
  try {
    const apiCall = yield call(fetchRequest, authUrls.login, {
      method: 'GET'
    })

    if (apiCall.ok) {
      const apiData = yield apiCall.json()

      yield put(authAction.signInSuccess(apiData.data))
    }
  } catch (error) {
    yield put(authAction.signInFailure('LOGIN ERROR'))
  }
}
function* logout() {
  try {
    yield put(authAction.logout())
  } catch (error) {
    yield put(authAction.signInFailure('LOGOUT ERROR'))
  }
}
function* googleSaga() {
  try {
    console.log('googleSign')
    yield put(authAction.signInStart())

    const res = {
      status: 'Success',
      data: {fullName: 'George', email: 'email'}
    }

    yield put(authAction.signInSuccess(res.data))

    // if (res.error) {
    //   yield put(fetchError(res.error))
    // } else {
    //   yield put(fetchSuccess(res))
    // }
  } catch (err) {
    console.log(err)
    yield put(authAction.signInFailure(err))
  }
}
function* signInSaga({payload}: SignInRequestActionT) {
  try {
    yield put(authAction.signInStart())
    const apiCall = yield call(fetchRequest, authUrls.signIn, {
      body: payload
    })

    if (!apiCall.ok) {
      const apiData = yield apiCall.json()

      toast.warn(apiData.message)
      yield put(authAction.signInFailure(`${apiData.message}`))
    } else {
      const apiData = yield apiCall.json()

      Cookies.set('Authorization', apiData.token)
      yield put(authAction.signInSuccess(apiData.data))
    }
  } catch (err) {
    toast.error('ðŸ”¥LOGIN ERROR * signInFetch * ðŸ”¥')
    yield put(authAction.signInFailure('LOGIN ERROR'))
  }
}
function* signUpSaga({payload}: SignUpRequestActionT) {
  try {
    yield put(authAction.signInStart())

    const apiCall = yield call(fetchRequest, authUrls.signUp, {
      body: pickBy(payload, identity)
    })
    const apiData = yield apiCall.json()

    if (!apiCall.ok) {
      toast.warn(apiData.message)
      yield put(authAction.signInFailure(`${apiData.message}`))
    } else {
      Cookies.set('Authorization', apiData.token)
      yield put(authAction.signInSuccess(apiData.data))
    }
  } catch (err) {
    console.log(err)
    yield put(authAction.signInFailure(err))
  }
}

/**
 * Watchers
 */
function* loginWatcher() {
  yield takeLatest(SignInTypes.login, login)
  yield takeLatest(SignInTypes.logoutSaga, logout)
}
function* authWatcher() {
  yield takeEvery(SignInTypes.signInSaga, signInSaga)
  yield takeEvery(SignInTypes.signUpSaga, signUpSaga)
  yield takeEvery(SignInTypes.googleSignSaga, googleSaga)
}

export function* authSaga() {
  yield all([fork(loginWatcher), fork(authWatcher)])
}
