import React, {FC} from 'react'
import {useSelector} from 'react-redux'
import List from '@material-ui/core/List'
import DrawerMui from '@material-ui/core/Drawer'
import clsx from 'clsx'

import {MenuDrawerLink} from '../../atoms/Links'
import {useDrawerS as useStyle} from './themes'
import {publicLinks, privateLinks} from '../../../router'
import {IStore} from '../../../interfaces/Store'
import {IIDrawer} from './ts'

export const Drawer: FC<IIDrawer> = ({isShow, hideDrawer}) => {
  const classes = useStyle()
  const $isAuth = useSelector((state: IStore) => state.$authReducer.isAuth)

  const clsxNames = {
    drawer: clsx(classes.drawer, {
      [classes.drawerOpen]: isShow,
      [classes.drawerClose]: !isShow
    }),
    classes: {
      paper: clsx(classes.paper, {
        [classes.drawerOpen]: isShow,
        [classes.drawerClose]: !isShow
      })
    }
  }

  return (
    <DrawerMui variant="permanent" className={clsxNames.drawer} classes={clsxNames.classes}>
      <List className={classes.list}>
        {publicLinks.map((linkProps, index) => (
          <MenuDrawerLink key={index} {...linkProps} onClick={hideDrawer} />
        ))}
        {$isAuth &&
          privateLinks.map((linkProps, index) => (
            <MenuDrawerLink key={index} {...linkProps} onClick={hideDrawer} />
          ))}
      </List>
    </DrawerMui>
  )
}
