import {SignInTypes} from './types'
import {IErrors, ISignInPayload, ISignUpPayload, IProfile} from '../../interfaces/Store'

/**
 * Simple Actions
 */
export const logout = () => ({type: SignInTypes.logout})
export const signInStart = () => ({type: SignInTypes.start})
export const signInSuccess = (payload: IProfile) => ({type: SignInTypes.success, payload})
export const signInFailure = (payload: IErrors) => ({type: SignInTypes.failure, payload})

/**
 * Sagas Actions
 */
export const loginSaga = () => ({type: SignInTypes.login})
export const googleSaga = () => ({type: SignInTypes.googleSignSaga})
export const signInSaga = (payload: ISignInPayload) => ({type: SignInTypes.signInSaga, payload})
export const signUpSaga = (payload: ISignUpPayload) => ({type: SignInTypes.signUpSaga, payload})
