import React, {FC, Ref, forwardRef} from 'react'
import {Link, LinkProps} from 'react-router-dom'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import {useDrawerLinkS as useStyle} from './themes'
import {ILink} from '../../../interfaces/Router'

export const MenuDrawerLink: FC<ILink> = ({text, to, Icon, onClick}) => {
  const classes = useStyle()

  const forward: LinkProps['component'] = forwardRef((props, ref: Ref<HTMLDivElement>) => (
    <Link ref={ref} {...props} />
  ))

  return (
    <ListItem button to={to} component={forward} className={classes.root} onClick={onClick}>
      <ListItemIcon>
        <Icon className={classes.icon} />
      </ListItemIcon>
      <ListItemText primary={text} />
    </ListItem>
  )
}
